<template>
  <div class="box">
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img v-lazy="image" height="200" />
      </van-swipe-item>
    </van-swipe>

    <van-grid class="icons">
      <van-grid-item icon="fire" text="" @click="show = true" />
      <van-grid-item icon="bars" text="" @click="dzShow = true" />
      <van-grid-item icon="ellipsis" text="" />
      <van-grid-item icon="ellipsis" text="" />
    </van-grid>

    <van-action-sheet
      v-model="show"
      :actions="actions"
      cancel-text=""
      description="--"
      @select="onSelect"
    />

    <van-action-sheet
      v-model="dzShow"
      :actions="dzActions"
      cancel-text=""
      description=""
      @select="onDzSelect"
    />
  </div>
</template>

<script>
import Vue from "vue";
import { ActionSheet, Swipe, SwipeItem, Lazyload, Grid, GridItem } from "vant";
Vue.use(Lazyload);

export default {
  components: {
    [ActionSheet.name]: ActionSheet,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Grid.name]: Grid,
    [GridItem.name]: GridItem,
  },
  data() {
    return {
      show: false,
      dzShow: false,
      actions: [
        { id: "primary", name: "" },
        { id: "intermediate", name: "" },
        { id: "senior", name: "" },
      ],
      dzActions: [
        { id: "primary", name: "" },
        { id: "intermediate", name: "" },
        { id: "senior", name: "" },
      ],
      images: [
        require("@/assets/images/001.png"),
        require("@/assets/images/002.png"),
        require("@/assets/images/003.png"),
        require("@/assets/images/004.jpg"),
      ],
    };
  },
  methods: {
    onSelect(item) {
      this.show = false;
      this.$router.push(`/draw/${item.id}`);
    },
    onDzSelect(item) {
      this.dzShow = false;
      this.$router.push(`/actionLibrary?id=${item.id}`);
    },
  },
};
</script>

<style lang="stylus" scoped>
.box {
  img {
    width: 100%;
  }

  >>> .van-hairline--top::after {
    border-width: 0;
  }

  .icons {
    width: 90%;
    height: 80px;
    border-radius: 10px;
    margin: -30px auto 0;
    position: relative;
    z-index: 9;
    background: #fff;
    box-shadow: 0 0 5px #e6e6e6;

    >>> .van-grid-item {
      .van-grid-item__content {
        background: transparent;

        &::after {
          border-width: 0;
        }
      }
    }
  }
}
</style>